.letter {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

  color: #fff;
  text-shadow: 2vmin 2vmin 0 rgba(0, 0, 0, .6);
  font-size: 40vmin;
  font-family: 'Patua One', cursive;

  -webkit-text-stroke-width: 1vmin;
  -webkit-text-stroke-color: var(--theme);
}

.letter span {
  position: relative;
  top: -5vmin;
  opacity: 0;
  -webkit-animation-name: reveal;
          animation-name: reveal;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

.letter:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: .5rem;
  background: #fff;
  -webkit-animation-name: completion;
          animation-name: completion;
  -webkit-animation-duration: inherit;
          animation-duration: inherit;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes reveal {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes reveal {
  from { opacity: 0; }
  to { opacity: 1; }
}

@-webkit-keyframes completion {
    from { width: 0; }
    to { width: 100%; }
}

@keyframes completion {
    from { width: 0; }
    to { width: 100%; }
}



