.switch {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: .8em;
}

.switch label {
  display: inline-block;
  width: 100%;
  padding: 0 .5em;
  text-align: right;
}

.switch input {
  -webkit-appearance: button;
  position: relative;
  display: inline-block;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 2.75rem;
  height: 1.5rem;
  background: #dfdfdf;
  border-radius: 1.5rem;
  cursor: pointer;
  outline: none;
  -webkit-transition: background .4s;
  -o-transition: background .4s;
  transition: background .4s;
}

.switch input:checked {
  background: #66bbff;
  background: var(--theme, #66bbff);
}

.switch input:after {
  content: '';
  position: absolute;
  display: block;
  left: .2rem;
  top: .2rem;
  width: 1.1rem;
  height: 1.1rem;
  background: #fff;
  border-radius: 50%;
  -webkit-box-shadow: 0 0 0 rgba(0, 0, 0, 0);
          box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  -webkit-transition: left .4s, -webkit-box-shadow .4s;
  transition: left .4s, -webkit-box-shadow .4s;
  -o-transition: left .4s, box-shadow .4s;
  transition: left .4s, box-shadow .4s;
  transition: left .4s, box-shadow .4s, -webkit-box-shadow .4s;
}

.switch input:checked:after {
  left: 1.4rem;
  -webkit-box-shadow: 0 .05em .25em rgba(0, 0, 0, .25);
          box-shadow: 0 .05em .25em rgba(0, 0, 0, .25);
}



