.controls {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.controls .buttons {
  position: relative;
  display: inline-block;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  text-align: right;
}

.controls .buttons > * {
  margin-left: 0;
  background: rgba(255, 255, 255, .1);
  -webkit-transition: background .4s;
  -o-transition: background .4s;
  transition: background .4s;
}


.controls .buttons > *:hover {
  background: rgba(255, 255, 255, 1);
}

.controls .settings {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  margin-top: 0;
}



